import React from 'react'

import styled from 'styled-components'
import { Link } from 'react-router-dom'

const Img = styled.img`
    max-width:100%;
    background-color:black;
    opacity: 0.75;
`
const HPLink = styled(Link)`
    text-decoration:none;
    display:block;
    height:10rem;
    width:100%;
    margin-bottom:1rem;
    position:relative;
    overflow:hidden;
    transition:all linear 500;
&:hover {
    color:#CEFF51;
    p{
        background-color: black;
        padding:.25rem;
    }
    img{
        opacity: 1;
    }
  }
`
const LinkText= styled.p`
bottom:2rem;
font-size:1.5rem;
position:absolute;
bottom:25px;
text-align:center;
width:100%;
margin:0;
`
export default ({to,imgSrc, text})=>(
    <HPLink to={to}>
        <Img src={imgSrc}/>
        <LinkText>
            {text}
        </LinkText>
    </HPLink>
)